import React, { lazy, Suspense, useState } from 'react';

const UI = lazy( () => import( './UI' ) );

const Entry = () => {

  const [ Form, setForm ] = useState(
    {
      studentName: '',
      FatherName: '',
      CNIC: '',
      MotherTongue: '',
      Occupation: '',
      MonthlyIncome: '',
      Surname: '',
      Religion: '',
      Gender: '',
      DateOfBirth: '',
      PlaceOfBirth: '',
      PreviousSchool: '',
      OfficeTelephone: '',
      ResidenceTelephone: '',
      Province: '',
      ResidentialAddress: ''
    }
  )

  const onChangeHandler = ( e ) => {

    const { name, value } = e.target; // EXTRACTING ATTRIBUTES
    const val = {
      ...Form,
      [name]: value
    }

    setForm( val );

  }

  return (
    <Suspense fallback={ <div>Loading...</div> }>
        <UI 
          onChangeHandler={ onChangeHandler }
        />
    </Suspense>
  );
}

export default Entry;